import { Component, OnInit } from '@angular/core';

import { User } from './user';

@Component({
    selector: 'login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})

export class LoginComponent{
    user = new User('','');
    
    debug: string;

    onClickShow(){
        this.debug = this.user.id + ' : ' + this.user.pwd;
    }

}


