import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  template: `
    <header style="text-align: center;">
      <h1 style="font-size:70px; background-color:black; color:white; height:100px;">
        <strong style="line-height:100px;">{{title}}</strong>
      </h1>
    </header>
    <router-outlet></router-outlet>
  `,
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = "PPLINK SHOPPINGMALL";

}



